.terms {
    padding: 234px 0 120px;

    h1,
    h3 {
        color: #fff;
    }

    &__block {
        display: flex;
        justify-content: space-between;

        &:not(:first-child) {
            margin-top: 40px;
        }

        &:not(:last-child) {
            padding-bottom: 40px;
            border-bottom: 1px solid rgba(#fff, 0.2);
        }

        p {
            &:not(:last-child) {
                margin-bottom: 24px;
            }
        }

        ul {
            li {
                position: relative;
                padding-left: 28px;

                &::after {
                    content: "";
                    position: absolute;
                    left: 10px;
                    top: 10px;
                    width: 3px;
                    height: 3px;
                    border-radius: 50%;
                    background-color: #fff;
                }

                &:not(:last-child) {
                    margin-bottom: 24px;
                }
            }
        }
    }

    &__wrapper {
        width: 100%;
        max-width: 884px;
    }
}