.portfolio {
    padding: 120px 0;

    ul {
        display: grid;
        grid-gap: 48px;
        grid-template-columns: 1fr 1fr 1fr;
        margin-bottom: 48px;
    }

    .rv-button {
        margin: 0 auto;
        width: fit-content;
        border: 1px solid #5e857a;
        color: #5e857a;
    }
}