body {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    font-family: Causten, sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.4;
    letter-spacing: 0.04em;
    color: #fff;
    background-image: linear-gradient(0deg, #18211f, #18211f);
    cursor: default;
}

.home {
    background-image: url("../images/bg.jpg");
    background-size: 100%;
    background-repeat: repeat;
}

.container {
    margin: 0 auto;
    padding: 0 15px;
    width: 100%;
    max-width: 1582px;
}

h1 {
    font-family: Causten-600, sans-serif;
    font-weight: 600;
    font-size: 72px;
    line-height: 1.2;
    letter-spacing: 0.015em;
    text-transform: uppercase;
    color: #ededed;
}

h2 {
    font-family: Causten-600, sans-serif;
    font-weight: 600;
    font-size: 48px;
    line-height: 1.2;
    letter-spacing: 0.015em;
    text-transform: uppercase;
    color: #ededed;
}

h3 {
    font-family: Causten-600, sans-serif;
    font-weight: 600;
    font-size: 24px;
    line-height: 1.2;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #ededed;
}

h4 {
    font-family: Causten-700, sans-serif;
    font-weight: 700;
    font-size: 18px;
    line-height: 1.2;
    letter-spacing: 0.015em;
    color: #ededed;
}

h5 {
    font-family: Causten-600, sans-serif;
    font-weight: 600;
    font-size: 16px;
    line-height: 1.1;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #ededed;
}

@font-face {
    src:
        local("Causten"),
        url("../fonts/Causten-Regular.woff2") format("woff2"),
        url("../fonts/Causten-Regular.woff") format("woff");
    font-family: Causten;
    font-weight: 400;
    font-style: normal;
    font-display: swap;
}

@font-face {
    src:
        local("Causten"),
        url("../fonts/Causten-Medium.woff2") format("woff2"),
        url("../fonts/Causten-Medium.woff") format("woff");
    font-family: Causten-500;
    font-weight: 500;
    font-style: normal;
    font-display: swap;
}

@font-face {
    src:
        local("Causten"),
        url("../fonts/Causten-SemiBold.woff2") format("woff2"),
        url("../fonts/Causten-SemiBold.woff") format("woff");
    font-family: Causten-600;
    font-weight: 600;
    font-style: normal;
    font-display: swap;
}

@font-face {
    src:
        local("Causten"),
        url("../fonts/Causten-Bold.woff2") format("woff2"),
        url("../fonts/Causten-Bold.woff") format("woff");
    font-family: Causten-700;
    font-weight: 700;
    font-style: normal;
    font-display: swap;
}

.rv-button {
    display: flex;
    align-items: center;
    padding: 0 32px;
    width: fit-content;
    min-height: 50px;
    border: 1px solid #fbd394;
    border-radius: 4px;
    font-family: Causten-600, sans-serif;
    font-weight: 600;
    font-size: 16px;
    text-transform: uppercase;
    color: #fbd394;

    p {
        height: 15px;
        line-height: 1;
    }
}

.preview {
    position: relative;

    .container {
        position: absolute;
        left: 50%;
        bottom: 120px;
        transform: translateX(-50%);
    }

    img {
        height: 440px;
        object-fit: cover;
    }
}

.send-form {
    &__wrapper {
        display: flex;
        justify-content: space-between;
        padding: 48px;
        background-color: #324541;
    }

    h2 {
        margin-bottom: 16px;
    }

    &__text {
        max-width: 545px;
    }

    input {
        margin-bottom: 38px;
        width: 100%;
        border-top: none;
        border-right: none;
        border-bottom: 1px solid #5e857a;
        border-left: none;
        background-color: transparent;
    }
}